*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#showWorksCode {
  max-width: 430px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

#showWorksCode > .worksCodeArea {
  height: 50vh;
  filter: hue-rotate(360deg);
  border: 4px solid;
  border-image: linear-gradient(45deg, gold, #ff1493) 1;
  animation: 3s linear infinite showWorksCodeAni;
  position: relative;
  overflow-y: auto;
}

@keyframes showWorksCodeAni {
  0% {
    filter: hue-rotate();
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

#showWorksCode > .worksCodeArea::-webkit-scrollbar {
  display: none;
}

#worksButtons {
  z-index: 5;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

#worksButtons > button {
  width: 3em;
  height: 1.8em;
  margin-top: 4px;
  margin-right: 4px;
}

#showWorks {
  width: 100%;
  height: 50vh;
  position: fixed;
  top: 50vh;
  left: 0;
}

